.pic {
    height: 230px;

    border: 5px solid #ffffff;
    overflow: hidden;
    cursor: pointer;
    border-radius: 10px;
    box-shadow: 3px 3px 5px lightgray;
}

.img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    transition: .2s linear;
}

.pic:hover .img {
    transform: scale(1.1);
}

.popup-image {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .9);
    height: 100%;
    width: 100%;
    z-index: 100;
    display: none;
}

.popup-image span {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 60px;
    font-weight: bolder;
    color: #ffffff;
    cursor: pointer;
    z-index: 100;
}

.imgPopup {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 5px solid #ffffff;
    border-radius: 5px;
    width: 750px;
    object-fit: cover;
}

@media (max-width: 768px) {
    .imgPopup {
        width: 95%;
    }
}
</style>
