<template>
  <div>
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      indicators
      background="#ababab"
      img-width="1024"
      img-height="480"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      class="estiloCarrusel"
    >
      <!-- Text slides with image -->
      <b-carousel-slide>
        <template #img>
        <a>
          <img
            class="d-block img-fluid w-100"
            width="1024"
            height="480"
            :src="imgUno"
            alt="image slot">
          </a>
        </template></b-carousel-slide>

      <!-- Slides with custom text -->
      <b-carousel-slide>
      
        <template #img>
          <a>
          <img
            class="d-block img-fluid w-100"
            width="1024"
            height="480"
            :src="imgDos"
            alt="image slot">
          </a>
        </template>
      
      </b-carousel-slide>

      <!-- Slides with image only -->
      <b-carousel-slide>
        <template #img>
          <a href="https://www.facebook.com/photo/?fbid=278135454343835&set=pb.100064899938580.-2207520000." target="_blank">
          <img
            class="d-block img-fluid w-100"
            width="1024"
            height="480"
            :src="imgTres"
            alt="image slot">
          </a>
        </template>
      </b-carousel-slide>

      <!-- Slides with img slot -->
      <!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment -->
      <b-carousel-slide>
        <template #img>
        <a href="https://www.facebook.com/UJEDFAZ/photos/pb.100064899938580.-2207520000./125166656307383/?type=3" target="_blank">
          <img
            class="d-block img-fluid w-100"
            width="1024"
            height="480"
            :src="imgCuatro"
            alt="image slot">
          </a>
        </template>
      
      </b-carousel-slide>

      <!-- Slide with blank fluid image to maintain slide aspect ratio -->
      <b-carousel-slide>
        <template #img>
        <a href="https://www.facebook.com/photo/?fbid=125446472946068&set=pb.100064899938580.-2207520000." target="_blank">
          <img
            class="d-block img-fluid w-100"
            width="1024"
            height="480"
            :src="imgCinco"
            alt="image slot">
          </a>
        </template>
      </b-carousel-slide>
    </b-carousel>
  </div>
</template>
<script>

export default {
  data: () => ({
    slide: 0,
    sliding: null,
    //Imagenes que se muestran en el carrusel, se pueden eliminar o aadir mas imagenes.
    imgUno: require('../../assets/images/carrusel/uno.jpg'),
    imgDos: require('../../assets/images/carrusel/dos.jpg'),
    imgTres: require('../../assets/images/carrusel/tres.jpg'),
    imgCuatro: require('../../assets/images/carrusel/cuatro.jpg'),
    imgCinco: require('../../assets/images/carrusel/cinco.jpg'),
  }),
  methods: {
    insertarRuta(ruta) {
      this.$router.push(ruta);
    },
    onSlideStart(slide) {
        this.sliding = true;
      },
    onSlideEnd(slide) {
        this.sliding = false;
      }
  },
};
</script>
<style>
    .carousel-item img {
       height: 350px;
       object-fit: cover;
    }
    .estiloCarrusel{
       cursor: pointer;
    }
    @media screen and (max-width: 960px){
      .carousel-item img {
       height: 250px;
       object-fit: cover;
    }
        }
</style>