            :rules="reglasNombre"></v-text-field>
          <v-text-field name="correo" label="Correo:" v-model="correo" outlined multi-line required
            :rules="reglasCorreo"></v-text-field>
          <v-text-field name="numberValue" label="Telfono:" v-model="numberValue" outlined multi-line type="number"
