<script>
import Carousel from './carrusel';
const emailjs = require('emailjs-com');
 
export default {
  data() {
    return {
      valid: true,
      nombre: '',
      correo: '',
      numberValue: null,
      areaInteres: '',
      descripcion: '',
      ofertaEducativa: ['Licenciaturas', 'Posgrados', 'Quejas y Sugerencias'],
      reglasNombre: [
        v => !!v || 'El nombre es requerido',
      ],
      reglasCorreo: [
        v => !!v || 'El correo es requerido',
      ],
      reglasTelefono: [
        v => !!v || 'Se requiere nmero telefonico',
      ],
      reglasDescripcion: [
        v => !!v || 'Agregue una descripcin',
      ],
      reglasAreaInteres: [
        v => !!v || 'El rea de inters es requerido',
      ],
      //Objeto que contiene la informacion que se muestra en la tarjeta de opciones
      accesos: [
        {
          id: 1,
          title: 'Directorio',
          src: require('../../assets/images/directorio.jpg'),
          ruta: '/directorio'
        },
        {
          id: 2,
          title: 'Posgrados',
          src: require('../../assets/images/posgrado.jpg'),
          ruta: '/posgrado'
        },
        {
          id: 3,
          title: 'Eventos',
          src: require('../../assets/images/eventos.jpg'),
          ruta: '/actividades-eventos'
        },
      ],
      //Objeto que contiene la informacion que se muestra en el contenedor de noticias relevantes
      card: [
        {
          title: 'Resultados de Admisin Nuevo Ingreso a Maestra en Agricultura Orgnica Sustentable (MAOS) Semestre A2023.',
          dia: '20',
          mes: 'Enero',
          des: 'Maestra en Agricultura Orgnica Sustentable (MAOS).',
          src: require('../../assets/images/noticia_1.jpg'),
          ruta: '/actividades-eventos'
        },
        {
          title: 'AVISO IMPORTANTE',
          dia: '2019',
          mes: 'Febrero',
          des: 'Atencin alumnos de la Maestra en Ciencias en "Sistemas de Produccin Agropecuaria" y el Doctorado en Ciencias en "Manejo Sustentable de los Recursos Naturales en Zonas ridas y Semiridas", se les notifica que tienen hasta el da 15 de diciembre del 2019, para titularse ya que estos programas se darn de baja definitiva.',
          src: require('../../assets/images/noticia_2.jpg'),
          ruta: '/actividades-eventos'
        },
        {
          title: 'Resultados de Admisin Nuevo Ingreso a Maestra en Agricultura Orgnica Sustentable (MAOS) Semestre B2019.',
          dia: '2019',
          mes: 'Julio',
          des: 'Maestra en Agricultura Orgnica Sustentable (MAOS).',
          src: require('../../assets/images/noticia_3.jpg'),
          ruta: '/actividades-eventos'
        },
      ],
    }
  },
  components: { Carousel },
  //Metodos utilizados
  methods: {
    //Metodo para redireccionar a otra ruta(pestaa) de la pgina
    insertarRuta(ruta) {
      this.$router.push(ruta);
    },
    //Metodo para enviar correos electronicos mediante el form (utiliza la libreria emailjs)
    enviar(e) {
      (function () {
        emailjs.init('AX4gnk3cn22arhn8f');
      });

      e.preventDefault();
      const serviceID = 'default_service';
      const templateID = 'template_a5thxei';

      if (this.$refs.form.validate()) {
        try {
          emailjs.sendForm(serviceID, templateID, e.target, 'AX4gnk3cn22arhn8f', {
            areaInteres: this.areaInteres,
            nombre: this.nombre,
            correo: this.correo,
            numberValue: this.numberValue,
            descripcion: this.descripcion
          })
          alert("ENVIADO CORRECTAMENTE");
          this.$refs.form.reset();
        } catch (e) {
          alert("NO SE ENVIO EL CORREO");
        }
      }
    },
  },
}
</script>
