<template>
    <v-app>
        <!--TARJETA QUE MUESTRA LA RUTA EN LA QUE ACTUALMENTE SE ENCUENTRA-->
        <v-card class="estiloBreadandTitles my-auto hidden-sm-and-down">
            <v-breadcrumbs :items="obtRuta" class="estiloRutaBread">
                <template v-slot:item="{ item }">
                    <v-breadcrumbs-item @click="insertarRuta(item.ruta)" :disabled="item.disabled">
                        {{ item.text.toUpperCase() }}
                    </v-breadcrumbs-item>
                </template>
            </v-breadcrumbs>
        </v-card>
        <!--TARJETA QUE CONTIENE EL TITULO DE LA RUTA ACTUAL JUNTO CON UNA IMAGEN QUE LA REPRESENTA-->
        <v-card class="my-auto hidden-sm-and-down">
            <v-img :src="imgTitle" class="estiloimgTitleHeader">
                <div class="estiloBreadandTitles">
                    <h1 class="my-auto hidden-sm-and-down">Actividades y Eventos</h1>
                </div>
            </v-img>
        </v-card>
        <!--CONTENEDOR QUE SOLO SE MUESTRA SI SE ENCUENTRA EN UN DISPOSITIVO MOVIL-->
        <div class="hidden-md-and-up">
            <!--BAR DE NAVEGACION QUE ACTIVA Y DESACTIVA LA LISTA DE OPCIONES-->
            <v-app-bar dense dark>
                <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
                <v-toolbar-title>Descargas</v-toolbar-title>
            </v-app-bar>
            <!--CONTENEDOR QUE TIENE LA LISTA DE LAS OPCIONES, SOLO SE ACTIVA MEDIANTE LA BARRA DE NAVEGACION (AppBar)-->
            <div>
                <v-navigation-drawer v-model="drawer" temporary app width="280" height="100vh">
                    <v-list rounded>
                        <v-list-item-group color="red">
                            <v-list-item v-for="n in opciones" :key="n.title" :value="n"
                                @click="drawer = false; itemSeleccionado(n.show)">
                                <v-list-item-title>
                                    {{ n.title }}
                                </v-list-item-title>
                            </v-list-item>
                            <v-divider class="my-2"></v-divider>
                            <v-list-item link color="grey-lighten-4">
                                <v-list-item-title @click="insertarRuta('/oferta-educativa')">
                                    Regresar
                                </v-list-item-title>
                            </v-list-item>
                        </v-list-item-group>
                    </v-list>
                </v-navigation-drawer>
                <!--CONTENEDOR DONDE SE MUESTRA LOS PDFS (segun el seleccionado)-->
                <div style="margin-top: 5%;">
                    <object v-show="descarga1" class="pdfview" type="application/pdf"
                        data="/pdfs/descargas/Normatividad_tesis.pdf"></object>
                    <object v-show="descarga2" class="pdfview" type="application/pdf"
                        data="/pdfs/descargas/reglamento_dep.pdf"></object>
                    <object v-show="descarga3" class="pdfview" type="application/pdf"
                        data="/pdfs/descargas/Formato_Evaluacion_Desempeno_Becario.pdf"></object>
                    <object v-show="descarga4" class="pdfview" type="application/pdf"
                        data="/pdfs/descargas/solicitud_maos.pdf"></object>
                    <object v-show="descarga5" class="pdfview" type="application/pdf"
                        data="/pdfs/descargas/solicitud.pdf"></object>
                </div>
            </div>
        </div>
        <!--CONTENEDOR QUE CONTIENE LOS PDF Y LA LISTA DE OPCIONES (no se muestra en dispositivos moviles)-->
        <v-container>
            <v-row class="mx-auto" style="margin-bottom: 5%;">
                <div class="my-auto hidden-sm-and-down">
                    <v-tabs v-model="tab" background-color="primary" class="p-5" dark>
                        <v-tab v-for="item in items" :key="item.tab">
                            {{ item.tab }}
                        </v-tab>
                        <v-tab-item class="pt-5">
                            <object class="pdfview" type="application/pdf"
                                data="/pdfs/descargas/Normatividad_tesis.pdf"></object>
                        </v-tab-item>
                        <v-tab-item class="pt-5">
                            <object class="pdfview" type="application/pdf"
                                data="/pdfs/descargas/reglamento_dep.pdf"></object>
                        </v-tab-item>
                        <v-tab-item class="pt-5">
                            <object class="pdfview" type="application/pdf"
                                data="/pdfs/descargas/Formato_Evaluacion_Desempeno_Becario.pdf"></object>
                        </v-tab-item>
                        <v-tab-item class="pt-5">
                            <object class="pdfview" type="application/pdf"
                                data="/pdfs/descargas/solicitud_maos.pdf"></object>
                        </v-tab-item>
                        <v-tab-item class="pt-5">
                            <object class="pdfview" type="application/pdf" data="/pdfs/descargas/solicitud.pdf"></object>
                        </v-tab-item>
                    </v-tabs>
                </div>
            </v-row>
        </v-container>
    </v-app>
</template>
<script>
export default {
    data: () => ({
        //Imagen que se muestra junto al titulo de la ruta actual
        imgTitle: require('../../assets/images/banners/descargas.jpg'),
        //Objeto que contiene la informacion de la ruta desde la pagina de inicio hasta la actual
        obtRuta: [
            {
                text: 'Inicio',
                disabled: false,
                ruta: '/',
            },
            {
                text: 'Descargas',
                disabled: true,
                ruta: '/',
            },
        ],
        tab: null,
        drawer: false,
        group: null,
        //Se utiliza para saber cual es el que se debe mostrar
        descarga1: true,
        descarga2: false,
        descarga3: false,
        descarga4: false,
        descarga5: false,
        //Objteto que contiene el numero de tab que se deben de mostrar (contiene el nombre(tab) y id(content))
        items: [
            { tab: 'Normatividad de tesis', content: 'uno' },
            { tab: 'Reglamento de Posgrado', content: 'dos' },
            { tab: 'Formato para Evaluacin del Desempeo del Becario', content: 'tres' },
            { tab: 'Solicitud de ingreso a Maestra', content: 'cuatro' },
            { tab: 'Solicitud de ingreso a Doctorado', content: 'cinco' },
        ],
        //Objeto que contiene la informacion de los pdf a descargar, se utiliza para la lista en los dispositivos moviles
        opciones: [
            {
                title: 'Normatividad de tesis',
                show: 1
            },
            {
                title: 'Reglamento de Posgrado',
                show: 2
            },
            {
                title: 'Formato para Evaluacin del Desempeo del Becario',
                show: 3
            },
            {
                title: 'Solicitud de ingreso a Maestra',
                show: 4
            },
            {
                title: 'Solicitud de ingreso a Doctorado',
                show: 5
            },
        ],
    }),
    //METODOS UTILIZADOS EN ESTA RUTA
    methods: {
        //Metodo para mostrar unicamente el pdf seleccionado, los demas seran desactivados
        itemSeleccionado(show) {
            this.descarga1 = false;
            this.descarga2 = false;
            this.descarga3 = false;
            this.descarga4 = false;
            this.descarga5 = false;
            if (show == 1)
                this.descarga1 = true;
            else if (show == 2)
                this.descarga2 = true;
            else if (show == 3)
                this.descarga3 = true;
            else if (show == 4)
                this.descarga4 = true;
            else if (show == 5)
                this.descarga5 = true;
        },
        insertarRuta(ruta) {
            this.$router.push(ruta);
        },
    },
}
</script>
<style>
.path {
    /*clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);*/
    /*clip-path: polygon(100% 0%, 75% 50%, 100% 100%, 0 100%, 0% 50%, 0 0);*/
    /*clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 10% 100%);
    */
    clip-path: polygon(0 0, 75% 0, 35% 100%, 0% 100%);
    background-color: rgb(255, 255, 255);
}

.pathint {
    clip-path: polygon(100% 0, 87% 0, 29% 100%, 100% 100%);
    background-color: black;
}

.pdfview {
    /* Centrado */
    margin: auto;
    display: block;
    /* Tamao */
    width: 80%;
    height: 100vh;
    /* Mejorar aspecto */
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width: 960px) {
    .pdfview {
        /* Centrado */
        margin: auto;
        display: block;
        /* Tamao */
        width: 98%;
        height: 100vh;
        /* Mejorar aspecto */
        border-radius: 20px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }
}
</style>